/* Dark Theme - Colours
1. Background (0dp elevation surface overlay)
2. Surface (with 1dp elevation surface overlay)
3. Primary
4. Secondary
5. On background
6. On Surface
7. On Primary
8. On Secondary

1.#121212
2.#121212
3.#BB86FC
4.#03DAC6
5.#FFFFFF
6.#FFFFFF
7.#000000
8.#000000



| elevation | overlay |   hex   |
| --------- | ------- | ------- |
|   00dp    |    0%   | #121212 |
|   01dp    |    5%   | #1e1e1e |
|   02dp    |    7%   | #222222 |
|   03dp    |    8%   | #242424 |
|   04dp    |    9%   | #272727 |
|   06dp    |   11%   | #2c2c2c |
|   08dp    |   12%   | #2e2e2e |
|   12dp    |   14%   | #333333 |
|   16dp    |   15%   | #343434 |
|   24dp    |   16%   | #383838 |


Component elevation values
1. Nav drawer: 16dp
2. App bar: 4dp
3. Card: 1dp to 8dp
4. FAB: 6dp
5. Button: 2dp to 8dp
6. Dialog: 24dp
*/

.App {
  font-family: 'Sawarabi Gothic';
}

section {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: lightgray;
  color: black;
  }

/* HOME SECTION */

#home {
  height: 100vh;
  background-color: #430391;
  color: white;
}

#projects {
  padding-top: 5vh;
  height: 100vh;
  background-color: whitesmoke;
}




#HomeDiv {
  position: absolute;
  padding-left: 0%;
  top: 20%;
}

#HomeP {
  margin-bottom: 5px;
  font-size: 5vh;
  color: #64ffda;
}

#HomeH {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 15vh;
}

#HomeH2 {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 5vh;
}


/* ABOUT SECTION */

#AboutRow {
  display: flex;
  width: 85%;
  height: 80%;
}

#AboutColumn {
  flex: 50%;
  margin-right: 50px;
}

#AboutH1 {
  font-size: 5vh;
  color: #430391;
  margin-bottom: -10px;
}

#AboutP {
  font-size: 3vh;
} 

#AboutSkill {
  font-size: 4vh;
  color: #430391;
  margin-bottom: -20px;

}

#AboutUL {
  display: flex;
  align-items: center;
  list-style: none;
  margin-bottom: -5px;
  margin-top: 25px;
}

#AboutUL > li {
  position: relative;
  margin-right: 50px;
  color: black;
  font-size: 3vh;
}

#AboutUL > li:before {
  position: absolute;
  content: "";
  left: -30px;
  top: 8px;
  background-color: #430391;
  width: 10px;
  height: 10px;
  border-radius: 10px;
}

#AboutUL > li:first-child:before {
  display: none;
}

/* NAVIGATION SECTION */

.nav {
  display: flex;
  align-items: center;
  /* position: -webkit-sticky; */
  position: fixed;
  z-index: 2;
  height: 12vh;
  width: 100%;  
  background-color:#2b0465;
  box-shadow: 0 0.125rem 0.25rem 0 rgb(0 0 0 / 11%);
  color: white;
  /* box-shadow: none; */

}

.nav__container {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.nav__container__actions {
  display: inline-flex;
  flex-flow: row nowrap;
  padding-left: 5vh;
  flex: 1;
}

.active {
  font-weight: bold;
  color: #64ffda;
}

.nav_ul {
  display: flex;
  gap: 3vh;
  list-style: none;
  padding: 0;
}

.nav_li {
  cursor: pointer;
  font-size: 4vh;
}

.nav_li:hover {
  color: #64ffda;
}

.icons {
  cursor: pointer;
  align-items: right;
  list-style: none;
  padding: 1vh;
  color: white;
  font-size: 6vh;
}

a:hover {
  color: #64ffda;
}

a {
  color: white;;
  text-decoration: none;
}

.HomeHeader {
  color:white;
}

/* PROJECT  */

.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1%;
  justify-content: center;
  align-items: center;
  padding-left: 20vh;
  padding-right: 20vh;
  margin-top: -15vh;
}

.grid-item {
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  padding: 1rem;
  text-align: center;
  min-height: 30vh;
  min-width: 30vh;
  font-size: 60%;
}

.iconsP {
  cursor: pointer;
  align-items: right;
  list-style: none;
  color: black;
  transition: color 0.2s ease-in-out;
  font-size: 7vh;
}

.iconsP:hover {
  color: #2b0465;
}
